export default {
	props: {
		bookDetail: {
			type: Object,
			default() {
				return {}
			}
		}
	},
	data() {
		return {
			publisher: '',
			anthor: '',
			price: '',
			time: ''
		}
	},
	watch: {
		bookDetail(newValue) {
			const detailArr = newValue.abstract.split('/')
			this.anthor = detailArr[0]
			this.publisher = detailArr[detailArr.length - 3]
			this.time = detailArr[detailArr.length - 2]
			this.price = detailArr[detailArr.length - 1]
		}
	}
	
}
